<template>
<!--选择建筑-->
<div class="selectBuilding">
	<header>
		<form onsubmit="return false" class="topForm">
			<input type="text" v-model="projectName" ref="searchBuilding" @blur="isSearch=!isSearch" />
			<span @click.prevent="beSearch" v-show="isSearch">
				<i class="icon iconfont icon-search"></i>
				搜索建筑名称
			</span>
		</form>
	</header>
	<div :class="setClassName">
		<ul class="mesList" v-if="!!searchingArr">
			<!--buildingArrs要改成  searchingArr-->
			<li v-for="(item,index) in searchingArr" v-if="!!item" @click.prevent="toRepairOrSpit(item)" class="clear" :key="index" :class="distanceClass(item.distance)">
				<div class="mesListLeft fl">
					<img src="../../../assets/imgleft.jpg" />
				</div>
				<div class="mesListRight fl">
					<div>
						<span>{{item.name}}</span>
						<span v-if="item.repairstatus==1&&!toggleDistance(item.distance)" class="canRepairs">
		        			可以报修
		        		</span>
						<span v-else-if="item.repairstatus==2" class="needVerify">
		        			需要密码验证
		        		</span>
						<span v-else-if="toggleDistance(item.distance)" class="cantRepairs">
                            	超过5公里，不能报修
                        </span>
					</div>
					<div class="fr">
						<span>{{calcDistance(item.distance)}}</span>
					</div>
				</div>
			</li>
		</ul>
	</div>
</div>
</template>
<script type="ecmascript-6">
import {
	mapActions,
	mapGetters
} from 'vuex'
import tools from 'utils/tools'
import {
	Loadmore,
	Toast
} from 'mint-ui'
import {
	getBuildingArr
} from 'api/repair'
import {
	weChatReady,
	getLocation
} from 'api/global'
import coordtransform from 'coordtransform'
import scanCode from '../common/scan'
export default {
	data() {
		return {
			isSpitslot: false,
			allLoaded: false,
			reqNum: 20, //下拉加载条数限制
			isSearch: true,
			projectName: '',
			sourceType: 'repair',
			searchingArr: null
		}
	},
	components: {
		'scan-code': scanCode
	},
	computed: {
		...mapGetters([
			'buildingArr'
		]),
		setClassName() {
			return this.isSpitslot ? 'mesListWrapNoScan' : 'mesListWrap'
		}
	},
	methods: {
		...mapActions(['SaveBuildingArr', 'SaveBuildingMes']),
		toggleDistance(distance) {
			//如果距离不存在或者距离大于5000
			return !distance || distance > 5000
		},
		distanceClass(distance) {
			if (this.toggleDistance(distance)) {
				//如果距离不存在或者距离大于5000，就给li添加class
				return 'beyondDistance'
			}
		},
		//      	roundDistance(tempBuildArr,result){
		//      		let fromLatitude=null,fromLongitude=null,toLatitude=null,toLongitude=null
		//              if(!!result&&!!result.latitude){
		//          		//fromLatitude：用户地理位置信息中的纬度
		//                  fromLatitude=Number.parseFloat(result.latitude)
		//              }
		//              if(!!result&&!!result.longitude){
		//              	//fromLongitude：用户地理位置信息中的经度
		//                  fromLongitude=Number.parseFloat(result.longitude)
		//              }
		//              tempBuildArr=tempBuildArr.map((eachBuild)=>{
		//                  let distance=null
		//                  if(!!fromLatitude&&!!fromLongitude){
		//                  	//如果用户的经度和纬度存在，那么走进这里
		//                      console.log(fromLatitude,fromLongitude,distance)
		//                      let tempDistance=eachBuild.distance||''
		//                  	let distanceArr=tempDistance.split(',')||[]
		//                  	//distanceArr的格式：['116.289548','40.052366']值是后台传过来的
		//                      if(distanceArr.length>1){
		//                          toLatitude=parseFloat(distanceArr[1])//后台传过来的建筑位置的纬度
		//                          toLongitude=parseFloat(distanceArr[0])//后台传过来的经度
		//                      }
		//                      let bd09togcj02=coordtransform.bd09togcj02(toLongitude,toLatitude)
		//                      toLatitude=bd09togcj02[1]
		//                      toLongitude=bd09togcj02[0]
		//                      //fromLatitude,fromLongitude,toLatitude,toLongitude
		//                      //用户的纬度，用户的经度，建筑的纬度，建筑的经度
		//                      distance=tools.getDistance(fromLatitude,fromLongitude,toLatitude,toLongitude)
		//              	}
		//                  let rstItem={
		//                      id:eachBuild.id,
		//                      sign:eachBuild.sign,
		//                      name:eachBuild.name,
		//                      distance:distance,
		//                      password:eachBuild.password,
		//                      repairstatus:eachBuild.repairstatus,
		//                      pid:eachBuild.pid
		//                  }
		//                  return rstItem
		//              })
		//              return tempBuildArr
		//      	},
		fetchData(name) {
			name = name.trim()
			if (name == '') {
				this.searchingArr = this.buildingArr
				return
			}
			//获取建筑列表
			this.searchingArr = this.buildingArr.map(building => {
				if (building.name.includes(name)) {
					console.log(building)
					return building
				}
			})
			console.log(this.searchingArr)
		},
		//         fetchLocation(res){
		//         		var tempBuildArr=res.data.Content||[]
		//	        	//微信授权结束
		//	        	//获取地理位置信息
		//	        	weChatReady().then(()=>{
		//	        		getLocation().then(locationRes=>{
		//	            	//重整数组，将距离计算出来
		//		            	tempBuildArr=this.roundDistance(tempBuildArr,locationRes)
		//		            	//将数据缓存
		//		            	this.SaveBuildingArr(tempBuildArr)
		//		        	}).catch(err=>{
		//		            	console.error(err)
		//		            	//如果没有获取到位置也缓存信息
		//		            	tempBuildArr=this.roundDistance(tempBuildArr)
		//		            	this.SaveBuildingArr(tempBuildArr)
		//		        	})
		//	        	}).catch(err=>{
		//	        		alert(JSON.stringify(err))
		//	        	})
		//         },
		calcDistance(distance) {
			if (!!distance) {
				if (distance < 100) {
					return '<100m';
				} else if (distance < 1000) {
					return Number.parseInt(distance * 10) / 10 + 'm';
				} else {
					return Number.parseInt(distance / 100) / 10 + 'km';
				}
			} else {
				return '--m'
			}
		},
		beSearch() {
			//点击span，让它隐藏，让搜索框获取焦点
			this.isSearch = false;
			this.$refs.searchBuilding.focus();
		},
		toRepairOrSpit(obj) {
			if (this.toggleDistance(obj.distance)) {
				Toast('您当前所在位置无法报修')
				return false
			}
			if (this.isSpitslot) {
				//跳转回吐槽页
				obj.isDialogShow = true
				this.$router.push({
					name: 'spitslot',
					params: obj
				})
			} else {
				//跳转到报修页
				this.$router.replace({
					name: 'repair',
					params: {
						repair: true,
						obj: obj
					}
				})
			}
			this.SaveBuildingMes(obj);
		},
		loadBottom() {
			this.allLoaded = true; // 若数据已全部获取完毕
			this.$refs.loadmore.onBottomLoaded();
		}
	},
	watch: {
		projectName(val) {
			this.fetchData(val)
		}
	},
	created() {
		document.title = "选择建筑"
		this.searchingArr = this.buildingArr
		//      	if(!!this.$route.params.isSpitslot){
		//      		//如果是从环境调节页面跳转过来的，那么走这里
		//      		this.isSpitslot=true
		//      		let locationRes=this.$route.params.locationRes//获取到的地理位置信息
		//      		//从后端获取数据
		//      		getBuildingArr('').then(res=>{
		//      			var tempBuildArr=res.data.Content||[]
		//      			tempBuildArr=this.roundDistance(tempBuildArr,locationRes)
		//	            	//将数据缓存
		//	            	this.SaveBuildingArr(tempBuildArr)
		//          	}).catch(readyErr=>{
		//              	console.error(readyErr)
		//          	})
		//      	}
	},
}
</script>
<style scoped lang='less'>
/*转换rem*/

@rem: 30rem;
.selectBuilding>* {
	background-color: #fff;
}

.selectBuilding header {
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
}

.topForm {
	width: 100%;
	height: 88/@rem;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	border-bottom: 1px solid #eeeeee;
	input {
		width: 688/@rem;
		height: 56/@rem;
		border: none;
		border: 1px solid #efefef;
		border-radius: 4px;
		padding-left: 20/@rem;
	}
	span {
		position: absolute;
		width: 688/@rem;
		height: 56/@rem;
		left: (750/@rem - 688/@rem)/2;
		top: 16/@rem;
		background-color: #efefef;
		text-align: center;
		line-height: 56/@rem;
		border-radius: 4px;
		font-size: 26/@rem;
		color: #9597a0;
	}
}

.mesListWrap {
	margin-top: 102/@rem;
}

.mesListWrapNoScan {
	margin-top: 102/@rem;
}

.mesList li {
	position: relative;
	.mesListLeft {
		width: 140/@rem;
		height: 140/@rem;
		border-radius: 4px;
		overflow: hidden;
		margin: 30/@rem;
		img {
			width: 100%;
		}
	}
	.mesListRight {
		width: calc(~"100% - "200/@rem);
		height: 200/@rem;
		border-bottom: 1px solid #ecf2f4;
		position: relative;
		div:nth-of-type(1) {
			width: calc(~"100% - "80/@rem);
			overflow-x: hidden;
			line-height: 56/@rem;
			padding-top: 30/@rem;
			span {
				display: inline-block;
			}
			span:nth-of-type(1) {
				width: 100%;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				font-size: 32/@rem;
				color: #000;
			}
			span.canRepairs {
				width: 120/@rem;
				font-size: 22/@rem;
				color: #44b197;
				border: 1px solid #44b197;
				border-radius: 3px;
				text-align: center;
				line-height: 38/@rem;
				height: 38/@rem;
			}
			span.needVerify {
				font-size: 24/@rem;
				color: #9597a0;
			}
			span.cantRepairs {
				color: #bbbcc2;
				font-size: 24/@rem;
			}
		}
		div:nth-of-type(2) {
			position: absolute;
			text-align: right;
			right: 30/@rem;
			top: 117/@rem;
			color: #9597a0;
			font-size: 24/@rem;
		}
	}
}

.mesList .beyondDistance {
	background-color: #f4f4f6;
}

.mesList .beyondDistance:before {
	content: '';
	position: absolute;
	z-index: 1;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(190, 190, 190, 0.1);
	opacity: 0.3;
	-webkit-blur: (2px);
}
</style>



